














































var shop;
//引用mSearch组件，如不需要删除即可
import mSearch from '@/components/search/mehaotian-search-revision.vue';
import twoGoods from '@/components/lama-goods-vue/two-goods.vue';
import headerSearch from '@/components/common/header-search.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
let userInfo;
export default {
	data() {
		return {
			headerPosition: "fixed",
			priceSort: '',
			saleSort: '',
			goodsList: [],
			sortType: '',
			page: 1,
			loadingType: 1,
			showLoadMore: false,
			keyword: '',
			cateId: 0
		}
	},
	onLoad: function(params) {
		this.keyword = params.keyword;
		if (params.cid) {
			this.cateId = params.cid;
		}
		userInfo = uni.getStorageSync('userLoginInfo');
		if (userInfo.identity) {
			this.identity = userInfo.identity;
		}
		shop = uni.getStorageSync('locationShop');
		//console.log(params);
		this.getGoodsList();
	},
	// 监听页面卸载
	onUnload() {
		this.goodsList = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		//console.log("onReachBottom");
		this.getGoodsList();
	},
	components: {
		//引用mSearch组件，如不需要删除即可
		mSearch,
		twoGoods,
		headerSearch,
		uniLoadMore
	},
	methods: {
		navigateBack() {
			uni.navigateBack();
		},
		setKeyword(data) {
			this.keyword = data;
			this.loadingType = 1;
			this.page = 1;
			this.goodsList = [];
			this.getGoodsList();
			//console.log(data);
		},
		// 获取商品分类
		// 获取商品列表
		getGoodsList: function() {
			if (this.page < 2) {
				uni.showLoading({
					title: "数据加载中..."
				});
			}
			let sort, data;
			if (this.sortType == 'price') {
				sort = this.priceSort;
			} else {
				sort = this.saleSort;
			}
			// console.log(this.keyword);
			if (this.keyword) {
				data = {
					shop_id: shop.id,
					page: this.page,
					name: this.keyword,
					limit: 10,
					type: this.sortType,
					sort: sort,
					identity:this.identity
				}
			} else {
				data = {
					shop_id: shop.id,
					page: this.page,
					cid: this.cateId,
					limit: 10,
					type: this.sortType,
					sort: sort,
					identity:this.identity
				}
			}
			//console.log(data);
			uni.request({
				url: this.apiUrl + 'goods/getGoodsList',
				method: 'GET',
				data: data,
				success: res => {
					if (res.data.code == 0) {
						// console.log(res.data.data);
						if (Array.isArray(res.data.data) && res.data.data.length === 0) {
							this.loadingType = 2;
							return;
						}
						if (res.data.data.length < 10) {
							this.loadingType = 2;
						}
						this.showLoadMore = true;
						this.page++;
						let goodsData = [];
						res.data.data.forEach((goods) => {
							goodsData.push(goods);
						});
						this.goodsList = this.goodsList.concat(goodsData);
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		// 根据价格或者销量进行排序
		sortList(e) {
			var el = e.currentTarget;
			//console.log(e);
			this.sortType = el.dataset.type;
			if (el.dataset.type == 'price') {
				if (this.priceSort == 'asc') {
					this.priceSort = 'desc';
				} else {
					this.priceSort = 'asc';
				}
				this.saleSort = '';
			} else if (el.dataset.type == 'sale') {
				if (this.saleSort == 'asc') {
					this.saleSort = 'desc';
				} else {
					this.saleSort = 'asc';
				}
				this.priceSort = '';
			} else {
				this.priceSort = '';
				this.saleSort = '';
				this.sortType = '';
			}
			
			this.loadingType = 1;
			this.goodsList = [];
			this.page = 1;
			this.getGoodsList();
		},
		blur() {
			uni.hideKeyboard()
		},
	}
}
